@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
.missions-section .mission-table{border-collapse:collapse;width:100%;border:1px solid #c7c7c7}.missions-section .mission-table thead tr{text-align:left}.missions-section .mission-table thead tr th{padding:.5rem;border:1px solid #c7c7c7}.missions-section .mission-table tbody tr{text-align:left}.missions-section .mission-table tbody tr td{padding:1rem;border:1px solid #c7c7c7}.missions-section .mission-table tbody tr td:last-child{width:15%;text-align:center}.missions-section .mission-table tbody tr td:nth-child(3){text-align:center;width:15%}.missions-section .mission-table tbody tr td span{padding:.3rem;background-color:#6d757d;border-radius:.4rem;color:#fff;font-size:.873rem}.missions-section .mission-table tbody tr td span.joined{color:#fff;background-color:#42a3b8}.missions-section .mission-table tbody tr td button{border:1px solid #6d757d;color:#6d757d;font-size:.8rem;padding:.3rem .6rem;cursor:pointer}.missions-section .mission-table tbody tr td button.joined{border:1.5px solid #e04f5c;color:#e04f5c}.missions-section .mission-table tbody tr:nth-child(2n+1){background-color:#f2f2f2}
.rd-rows{display:flex;flex-direction:column}*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Oswald",sans-serif;overflow-y:scroll;padding-left:25px;padding-right:25px}main{margin-top:25px;margin-bottom:25px}header{position:sticky;top:0;display:flex;align-items:center;grid-gap:25px;gap:25px;height:100px;padding-top:25px;padding-bottom:25px;border-bottom:1px solid gray;background:#fff}header img{height:50px}header h1{margin-right:auto}header nav{display:flex;grid-gap:20px;gap:20px}header nav a{text-decoration:none}header nav .active{text-decoration:underline}.rd-rows{grid-gap:25px;gap:25px}.profiles{display:flex;flex-direction:row;grid-gap:25px;gap:25px}.profiles .profile-column{display:flex;flex-direction:column;width:33.3%}.profiles .profile-column ul{border:1px solid gray;border-radius:5px;margin-top:25px;margin-bottom:25px}.profiles .profile-column li{list-style:none;padding:10px}.profiles .profile-column li+li{border-top:1px solid gray}
